p, label, a, span, button, input, select, table tr td, li{
  font-size:18px;
}
[data-dpr="2"] p, [data-dpr="2"] label, [data-dpr="2"] a, [data-dpr="2"] span, [data-dpr="2"] button, [data-dpr="2"] input, [data-dpr="2"] select, [data-dpr="2"] table tr td, [data-dpr="2"] li {
  font-size: 24px;
}
[data-dpr="3"] p, [data-dpr="3"] label, [data-dpr="3"] a, [data-dpr="3"] span, [data-dpr="3"] button, [data-dpr="3"] input, [data-dpr="3"] select, [data-dpr="3"] table tr td, [data-dpr="3"] li {
  font-size: 36px;
}
*{
  font-size:18px;
}
[data-dpr="2"] * {
  font-size: 24px;
}
[data-dpr="3"] * {
  font-size: 36px;
}
.font-ss{
  font-size:9px;
}
[data-dpr="2"] .font-ss {
  font-size: 12px;
}
[data-dpr="3"] .font-ss {
  font-size: 18px;
}
.font-s{
  font-size:12px;
}
[data-dpr="2"] .font-s {
  font-size: 16px;
}
[data-dpr="3"] .font-s {
  font-size: 24px;
}
.font{
  font-size:18px;
}
[data-dpr="2"] .font {
  font-size: 24px;
}
[data-dpr="3"] .font {
  font-size: 36px;
}
.font-l{
  font-size:27px;
}
[data-dpr="2"] .font-l {
  font-size: 36px;
}
[data-dpr="3"] .font-l {
  font-size: 54px;
}
.font-x{
  font-size:36px;
}
[data-dpr="2"] .font-x {
  font-size: 48px;
}
[data-dpr="3"] .font-x {
  font-size: 72px;
}
.font-xx{
  font-size:54px;}
[data-dpr="2"] .font-xx {
  font-size: 72px;
}
[data-dpr="3"] .font-xx {
  font-size: 108px;
}
.c-p{  color: pink;  }
.c-g{  color: #b2b2b2;  }
.padding-5{
  padding: 5px;
}
.padding-10{
  padding: 10px;
}
.padding-20{
  padding: 20px;
}
.padding-l-10{
  padding-left: 10px;
}



