
@-webkit-keyframes loadingPlus {
0% {
    background-position: 0 0
}
10% {
    background-position: 0 -50px
}
20% {
    background-position: 0 -100px
}
30% {
    background-position: 0 -150px
}
40% {
    background-position: 0 -200px
}
50% {
    background-position: 0 -250px
}
60% {
    background-position: 0 -300px
}
70% {
    background-position: 0 -350px
}
80% {
    background-position: 0 -400px
}
90% {
    background-position: 0 -450px
}
100% {
    background-position: 0 0
}
}
@keyframes loadingPlus {
0% {
    background-position: 0 0
}
10% {
    background-position: 0 -50px
}
20% {
    background-position: 0 -100px
}
30% {
    background-position: 0 -150px
}
40% {
    background-position: 0 -200px
}
50% {
    background-position: 0 -250px
}
60% {
    background-position: 0 -300px
}
70% {
    background-position: 0 -350px
}
80% {
    background-position: 0 -400px
}
90% {
    background-position: 0 -450px
}
100% {
    background-position: 0 0
}
}
.ui-loading-wrap .mask {
  position: absolute;
  width: 16px;
  width: 1rem;
  height: 50px;
  left: 50%;
  top: 50%;
  margin-left: -8px;
  margin-left: -.50rem;
  margin-top: -25px;
  background: url("../assets/images/common/loading.png") no-repeat;
  background-size: 1rem 500px;
  -webkit-animation: loadingPlus 400ms steps(1) infinite;
  animation: loadingPlus 400ms steps(1) infinite;
}
.ui-loading-wrap {
  position: fixed;
  top: 0;
  z-index: 10000;
  width: 100%;
  height: 100%;
  opacity: 0.4;
  background: #222;
}



